.navBar{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 8rem;
    margin: 0;
    align-items: center;
    /* background: var(--color-header); */
    font-size: 1.8rem;
}
.list , .navLinks{
    text-align: center;
    display: flex;
}
.list a, .navLinks a{
    color: var(--color-text);
    margin-right: 4rem ;
    font-size: large;
    text-decoration: none;
    justify-content: center;
}
.footer--navLinks a{
    margin-right: 4rem ;
    color: var(--color-text);
    justify-content: center;
    text-decoration: none;

}
/* .wrapLogo img{
    width: 8.9rem;
    height: 110%;
} */

.searchIcon , .shopIcon{
    margin-right: 2vw;
}
.noRightMargin{
    margin-right: 0;
}
.leftMargin{
    margin-left: 5vw ;
}

/* HeaderIcons */
.iconHeader{
    margin: 0;
}


/* Nav Link */
.menu{
    display: none;
    background-color: transparent;
    border: none;
    color: white;
}
.marginRight{
    margin-right: 2rem;
}
/* .hideBars{
    displ
} */




/*============================= CART */
.blurBackground{
    background-color: rgba(0, 0, 0,0.7);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 50;
    height: 100vh; 
    /* display: none; */
     width: 100vw; 
}
.wrap--cart{
    position: fixed;
    top: 10vh;
    left: 5vw;
    background-color: #F7F8FA;
    color: black;
    width: 90%;
    height: 80vh;
    border: var(--color-header) 2px solid;
    margin: auto;
    border-radius: 15px;
    z-index: 100;
    text-align: center;
    /* overflow-y: scroll; */
    /* height: 40vh; */
    /* display: none; */
}
.wrapCartBoxEmpty{
    
    margin: 10vh auto;
    display: flex;
    
    width: 60%;
    flex-direction: column;
}
.wrapCartBoxEmpty >*{
    margin: 1rem auto;
}

/* Invisble Request Box */
.requestInvisible{
    display: none;
}

.emptyCartImage{
    /* border: 2px solid red; */
    width: 30%;
    /* margin: 1rem auto ; */
}

.wrapCartBoxFull{
    border-radius: inherit;
}
.wrap--cartItems{
    background-color: var(--color-textAlt);
    overflow-y: scroll;
    height: 60vh;
}


.cart--heading{
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    font-size: 4rem;
    width: 80%;
}
.cart--heading p{
    margin-left: 0rem;
}






/* media queries */
@media screen and (max-width:767px){
    .menu{
        display: inline-block;
    }
    .closedMenu{
        display: block;
    }

    .wrap--navLinks{
        /* background-color: red; */
        /* z-index: 10000000; */
        color: var(--color-text);
        margin-top: 4rem;
        height: 100vh;
        width: 100vw;
    }
    .navLinks{
        flex-direction: column;
    } 
    .footer--navLinks a{
        margin-right: 2rem;
    }
}


@media screen and (max-width:500px) {
    .footer--navLinks a{
        margin-right: 1rem;
        font-size: 1.3rem;
    }
    .list a{
        margin-right: 2rem;
    }
}

@media screen and (max-width:376px) {
    .footer--navLinks a{
        font-size: 1.2rem;
    }
    .wrapLogo img{
        width: 2rem;
    }
    .list a{
        font-size: medium;
    }
}